import React, { useState } from "react";
import { Link, Navigate } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { useTranslation, Trans } from "react-i18next";

import { login } from "../../actions/auth";

const Login = () => {
  const isAuthenticated = useSelector(
    (state: any) => state.auth.isAuthenticated
  );

  const [formData, setFormData] = useState({
    email: "",
    password: "",
  });

  const { email, password } = formData;

  const onChange = (e: any) =>
    setFormData({ ...formData, [e.target.name]: e.target.value });

  const onSubmit = (e: any) => {
    e.preventDefault();
    login(email, password);
  };

  if (isAuthenticated) {
    return <Navigate to="/dashboard" />;
  }

  return (
    <section className="n-container">
      <div className="max-w-[400px] m-auto my-20">
        <h1 className="text-black text-5xl font-bold text-center">
          <Trans i18nKey="sign in" />
        </h1>
        <form className="form" onSubmit={onSubmit}>
          <div className="mt-4">
            <p>Email</p>
            <input
              type="email"
              placeholder="Email Address"
              className="w-full outline-0 bg-color1 px-4 py-2 border-b-4"
              name="email"
              value={email}
              onChange={onChange}
            />
          </div>
          <div className="mt-4">
            <p>Password</p>
            <input
              type="password"
              placeholder="Password"
              name="password"
              className="w-full outline-0 bg-color1 px-4 py-2 border-b-4"
              value={password}
              onChange={onChange}
            />
          </div>
          <button
            type="submit"
            className="px-4 py-2 border border-black w-full mt-5 rounded-md"
          >
            <Trans i18nKey="sign in" />
          </button>
        </form>
        <p className="my-1 text-center">
          Don't have an account? <Link to="/register">Sign Up</Link>
        </p>
      </div>
    </section>
  );
};

export default Login;
