import React, { useState, useEffect } from "react";
import { Link, Navigate } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { useTranslation, Trans } from "react-i18next";

import LanguageSelect from "./LanguageSelect";

const Navbar = () => {
  const dispatch = useDispatch();
  const isAuthenticated = useSelector(
    (state: any) => state.auth.isAuthenticated
  );

  const user: any = useSelector((state: any) => state.auth.user);

  const [isShow, toggleModal] = useState(false);
  const [isShowDrop, setShowDrop] = useState(false);
  const isConnected: boolean = false;

  return (
    <div>
      <div className="w-full bg-color1 py-6 z-20 border-b border-color2">
        <div className="n-container">
          <div className="flex justify-between items-center">
            <Link to={"/"}>
              <div className="flex items-center gap-3">
                <img src="/logo.svg" alt="logo" className="h-12" />
              </div>
            </Link>

            <div className="hidden sm:flex font-bold text-black gap-8">
              <Link to="/">
                <Trans i18nKey="about" />
              </Link>
              <Link to="/">
                <Trans i18nKey="faq" />
              </Link>
              <Link to="/">
                <Trans i18nKey="inquiry" />
              </Link>
            </div>

            <div className="flex gap-6 items-center">
              <LanguageSelect />

              {isAuthenticated ? (
                <div className="relative">
                  <div
                    className="flex items-center gap-3 text-black cursor-pointer"
                    onClick={() => setShowDrop(!isShowDrop)}
                  >
                    {/* <img
                    src={user ? user.avatar : ""}
                    alt=""
                    width={40}
                    className="rounded-3xl"
                  /> */}
                    {/* <p>{user ? user.name : ""}</p> */}
                  </div>
                  <div
                    className={`w-max p-4 text-white text-right text-xl right-0 border border-gray-500 rounded-xl bg-[#03041a] absolute top-20 ${
                      isShowDrop ? "" : "hidden"
                    }`}
                  >
                    <div className="border-b border-whites pb-3 mb-3">
                      {!isConnected ? (
                        <>
                          <button
                            className="text-white border border-white px-3 py-1 rounded"
                            // onClick={connectWallet}
                          >
                            Connect Wallet
                          </button>
                        </>
                      ) : (
                        <button className="text-white border border-white px-3 py-1 rounded">
                          {/* {formatAddress(blockchain.account)} */}
                        </button>
                      )}
                    </div>

                    <Link to={"/profile"} onClick={() => setShowDrop(false)}>
                      {" "}
                      <p className="">Profile</p>
                    </Link>
                    <Link
                      to={"/create-post"}
                      onClick={() => setShowDrop(false)}
                    >
                      {" "}
                      <p className="mt-2">Create Post</p>
                    </Link>
                    <Link to={"/create-nft"} onClick={() => setShowDrop(false)}>
                      {" "}
                      <p className="mt-2">Create NFT</p>
                    </Link>
                    <Link to={"/message"} onClick={() => setShowDrop(false)}>
                      {" "}
                      <p className="mt-2">Message</p>
                    </Link>
                    <Link
                      to={"/subscription"}
                      onClick={() => setShowDrop(false)}
                    >
                      {" "}
                      <p className="mt-2">Subscription</p>
                    </Link>
                    <hr className="mt-4" />
                    <p
                      className="mt-2 cursor-pointer"
                      onClick={() => {
                        // logout();
                        setShowDrop(false);
                      }}
                    >
                      Logout
                    </p>
                  </div>
                </div>
              ) : (
                <div className="hidden sm:flex gap-4">
                  <Link to={"login"}>
                    <button className="border border-black h-10 px-2 bg-white text-black rounded font-bold">
                      <Trans i18nKey="sign in" />
                    </button>
                  </Link>
                  <Link to={"signup"}>
                    <button className="border border-black h-10 px-2 bg-white text-black rounded font-bold">
                      <Trans i18nKey="sign up" />
                    </button>
                  </Link>
                </div>
              )}

              <p
                className="none sm:hidden font-bold text-black pointer"
                onClick={() => toggleModal(true)}
              >
                &#9776;
              </p>
            </div>
          </div>
        </div>

        <div
          className={` ${
            isShow ? "" : "hidden"
          } bg-[#263240] fixed left-0 top-0 h-screen w-full`}
        >
          <p
            className="none sm:hidden font-bold text-white text-right pointer m-5 mt-10"
            onClick={() => toggleModal(false)}
          >
            &#9776;
          </p>

          <div className="text-white text-center mt-12">
            <Link to="/" onClick={() => toggleModal(false)} className="py-4">
              <p>?</p>
            </Link>
            <Link to="/" onClick={() => toggleModal(false)} className="py-4">
              <p></p>
            </Link>
            <Link to="/" onClick={() => toggleModal(false)} className="py-4">
              <p></p>
            </Link>
          </div>

          <div className="flex justify-center gap-4 mt-10">
            <Link to={"login"} onClick={() => toggleModal(false)}>
              <button className="px-4 text-black h-10 bg-gradient-blue rounded font-bold">
                
              </button>
            </Link>
            <Link to={"signup"} onClick={() => toggleModal(false)}>
              <button className="border border-black h-10 px-2 bg-white text-black rounded font-bold">
                
              </button>
            </Link>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Navbar;
